import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { auth } from 'firebase/app';
import { ToastController } from '@ionic/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.page.html',
  styleUrls: ['./signup.page.scss'],
})
export class SignupPage {
  username: string = '';
  password: string = '';
  constructor(public afAuth: AngularFireAuth, public toastController: ToastController, private router: Router) {}

  async signup(){
    const { username, password } = this;
    console.log(username);
    try {
      const res = await this.afAuth.createUserWithEmailAndPassword(username + '@gmail.com', password);
      console.log(res);
      if(res.operationType == 'signIn'){
        this.presentToast('Successfully User Created');
        this.router.navigate(['/']);
      }
    } catch (err) {
      console.log(err);
      if(err.code == 'auth/invalid-email'){
        this.presentToast('Enter the Username');
      }
      if(err.code == 'auth/weak-password'){
        this.presentToast('The password must be 6 characters long or more.');
      }
      if(err.code == 'auth/email-already-in-use'){
        this.presentToast('The email address is already in use by another account.');
      }
    }
  }
  user(event){
    console.log(event);
    this.username = event;
  }
  pass(event){
    console.log(event);
    this.password = event;
  }


  async presentToast(msg) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 2000
    });
    toast.present();
  }
}
