import { Component } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { auth } from 'firebase/app';
import { ToastController } from '@ionic/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: ['tabs.page.scss']
})

export class TabsPage {

  username: string = '';
  password: string = '';
  constructor(public afAuth: AngularFireAuth, public toastController: ToastController, private router: Router) {}

  async login(){
    const { username, password } = this;
    console.log(username);
    try {
      const res = await this.afAuth.signInWithEmailAndPassword(username + '@gmail.com', password);
      console.log(res);
      if(res.operationType == 'signIn'){
        this.presentToast("Your successfully Logged In");
        this.router.navigate(['/dashboard']);
      }
    } catch (err) {
      console.dir(err.code);
      if(err.code == "auth/user-not-found"){
        this.presentToast("User not Found")
      }
      if(err.code == "auth/wrong-password"){
        this.presentToast("Please Check Your Password")
      }
    }
  }

  async presentToast(msg) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 2000
    });
    toast.present();
  }
}
